module.exports = function () {
    if ($('.main-slider').length){
		 
		 var mainSlider = $('.main-slider'),
			  countCurrent = $(".main-slider__count-current"),
			  countTotal = $(".main-slider__count-total");
		 
		
	
		
		mainSlider.on('changed.owl.carousel', function(event) {
			var countThis = event.item.index - 1;
			
			if (countThis > event.item.count ){
				countThis = 1;
			}
			if (countThis < 1){
				countThis = event.item.count;
			}
			
			countCurrent.text(countThis);
		});
		
		mainSlider.on('initialized.owl.carousel', function(event) {
			 countCurrent.text(1);
		});
		 
		mainSlider.owlCarousel({
			items: 1,
			nav:false,
			navText: ['prev', 'next'],
			loop: true,
			dots: false,
			margin: 0,
			mouseDrag: false,
			animateOut: 'fadeOut',
			//autoplay: true,
			oninitialized: function(event){
				countTotal.text(event.item.count);
			}
		}); 
		
		
		
		
		$('.main-slider__nav-prev').on("click", function() {
			 mainSlider.trigger('prev.owl.carousel');
		});
		
		$('.main-slider__nav-next').on("click", function() {
			 mainSlider.trigger('next.owl.carousel');
		});
		
		
		 
		
	} 
};
