module.exports = function () {
    const transitionDuration = 200;

    const macyOptions = {
        columns: 4,
        margin: {x: 10, y: 10},
        breakAt: {
            1160: {
                columns: 3
            },

            880: {
                columns: 2
            },

            600: {
                columns: 1
            }
        }
    };

    const mainChoices = [...document.querySelectorAll(".main-choices [data-macy-index]")];
    const choicesWrappers = [...document.querySelectorAll(".choices-section .choices-wrapper")];

    const containers = [...document.querySelectorAll(".choices")];
    const macyInstances = [];

    containers.forEach(item => {
        macyInstances.push(Macy({
            ...macyOptions,
            container: item,
        }));
    });

    mainChoices.forEach(node => {
        node.addEventListener("click", function (e) {
				
				if (!$(e.target).hasClass("tooltip")){
					const index = parseInt(this.getAttribute("data-macy-index"));
					const $choiceWrappers = $(choicesWrappers);
					const $activeWrappers = $choiceWrappers.filter(function () {
						 return $(this).hasClass("initialized")
					});
					const wrapperToOpen = $choiceWrappers.get(index);

					// Legacy
					if ($activeWrappers.length) {
						 $activeWrappers
							  .removeClass("initialized")
							  .fadeOut(transitionDuration, function () {
									$(wrapperToOpen)
										 .show()
										 .css("opacity", "0");

									macyInstances.forEach(instance => instance.recalculate(true));

									$(wrapperToOpen)
										 .hide()
										 .css("opacity", "1");

									$(wrapperToOpen)
										 .addClass("initialized")
										 .fadeIn(transitionDuration);
							  });

					} else {
						 $(wrapperToOpen)
							  .show()
							  .css("opacity", "0");

						 macyInstances.forEach(instance => instance.recalculate(true));

						 $(wrapperToOpen)
							  .hide()
							  .css("opacity", "1");

						 $(wrapperToOpen)
							  .addClass("initialized")
							  .fadeIn(transitionDuration);
					}
				}
            
        });
    });
};
