const throttle = require("lodash/throttle");

module.exports = function () {
    const blocks = document.querySelectorAll("[data-make-sticky]");

    blocks.forEach(item => {
        const showUntilIsntVisibleNode = document.querySelector(item.getAttribute("data-show-until-inst-visible"));

        const $clonedItem = $(item).clone();
        $(item.parentNode).append($clonedItem);

        $clonedItem.addClass("fixed");
			
	
        window.addEventListener("scroll", throttle(function () {
            const isShowUntilIsVisible = $(showUntilIsntVisibleNode).is(":visible");

            const endNode = document.querySelector(item.getAttribute("data-make-sticky-end"));

            const itemBoundingClientRect = item.getBoundingClientRect();
            const topOffset = itemBoundingClientRect.top  + itemBoundingClientRect.height;
				
				var scrollWindow = $(window).scrollTop(),
					 slider = $(".main-slider"),
					 offsetSlider = slider.offset().top;

				
            if (topOffset > 0) {
                $clonedItem.removeClass("active");
					 
            } else {
                if (endNode && isShowUntilIsVisible) {
                    const endNodeRect = endNode.getBoundingClientRect();
                    const availableScrollLength = endNodeRect.top + endNodeRect.height;
                    const isEndReached = availableScrollLength < window.innerHeight;

                    if (isEndReached) {
                        $clonedItem.removeClass("active");
                    } else {
                        
								if ($(".choice--main.checked").length){
									if (scrollWindow <= (offsetSlider + slider.height()/2)){
										$clonedItem.removeClass("active");
									}
									else{
										$clonedItem.addClass("active");
									}
								}
                    }
                } else {
                    //$clonedItem.addClass("active");
                }
					 /*
					 if (scrollWindow < (offsetSlider + slider.height() / 2)){
						$clonedItem.removeClass("active");
					}
					else{
						$clonedItem.addClass("active");
					}
					*/
            }
				
        }, 50));
		  
		  
    });
};
