module.exports = function () {
    const $toShow = $("[data-show-on-order]");
    //$toShow.hide();
	 
    $(document).on("click", ".meta-make-order-button", function (e) {
		 var $this = $(this);
		 if ($(".choice--main.checked").length){
			 $(".meta-make-order-button .status-bar-message").removeClass("active");
			//$toShow.show();
			$(document).scrollTo(document.querySelector(".make-order-section"), 1000);
		 }
		 else{
			 $(".meta-make-order-button .status-bar-message").addClass("active");
		 }
    });
	 
    $(document).on("click", ".make-order-footer .status-bar-submit.active", function () {
		 if ($(".choice--main.checked").length){
			 $(".make-order-footer .status-bar-message").removeClass("active");
		 }
		 else{
			 $(".make-order-footer .status-bar-message").addClass("active");
		 }
    });
	 
    $(document).on("click", function (e) {
		 var btnSubmit = '.meta-make-order-button',
			 msg = '.meta-make-order-button .status-bar-message';
			 
		 var btnSubmit2 = '.make-order-footer .status-bar-submit',
			 msg2 = '.make-order-footer .status-bar-message';
			 
		if (!$(btnSubmit).is(e.target)
			&& !$(msg).is(e.target)
			&& $(e.target).closest(btnSubmit).length === 0
			&& $(e.target).closest(msg).length === 0) {
				 $(msg).removeClass("active");
		}
		
		if (!$(btnSubmit2).is(e.target)
			&& !$(msg2).is(e.target)
			&& $(e.target).closest(btnSubmit2).length === 0
			&& $(e.target).closest(msg2).length === 0) {
				 $(msg2).removeClass("active");
		}
    });
};
