module.exports = function () {
    const transitionDuration = 400;
    const spoilers = [...document.querySelectorAll(".spoiler")];

    spoilers.forEach(item => {
        $(item).on("click", ".spoiler-dropdown", function () {
            const $container = $(this).closest(".spoiler");
            const $spoilerContentContainer = $container.find(".spoiler-content");

            if ($spoilerContentContainer.hasClass("expanded")) {
                $(this).removeClass("expanded");

                $spoilerContentContainer
                    .removeClass("expanded")
                    .addClass("closing")
                    .slideToggle(transitionDuration, function () {
                        $(this).removeClass("closing");
                    });
            } else {
                $(this).addClass("expanded");

                $spoilerContentContainer
                    .removeClass("expanded")
                    .addClass("expanding")
                    .slideToggle(transitionDuration, function () {
                        $(this).removeClass("expanding").addClass("expanded").attr("style", "");
                    });
            }
        });
    });
};
