module.exports = function () {
    const $wrapper = $(".adaptive-menu");

    $(document).on("click", "[data-menu-open]", function () {
        $wrapper.addClass("active");
        $("html").addClass("prevent-scroll");
    });

    $wrapper.on("click", "[data-menu-close]", function () {
        $(this).closest($wrapper).removeClass("active");
        $("html").removeClass("prevent-scroll");
    });

    // Click in background
    $wrapper.on("click", function (event) {
        const isClickedInBackground = $(event.target).is($wrapper);

        if (isClickedInBackground) {
            $(this).removeClass("active");
            $("html").removeClass("prevent-scroll");
        }
    });
};
